
/* 等级要求标签 */
.step-level-req {
  font-size: 0.8em;
  color: var(--theme-text-muted);
  font-style: italic;
  margin-left: 15px;
}

/* 步骤项容器 */
.step-items-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* 步骤项 */
.step-item {
  margin-bottom: 5px;
  line-height: 1.6;
}

.item-name {
  font-weight: bold;
  color: var(--theme-text-tertiary);
}

.item-desc {
  font-size: 0.9em;
  font-style: italic;
  color: var(--theme-text-muted);
  margin-left: 20px;
  display: block; /* 确保换行 */
  white-space: pre-line;
}

/* 空状态提示 */
.empty-slot {
  color: var(--theme-text-muted);
  font-style: italic;
  margin: 0;
}

/* 神位/神国网格布局 */
.god-tier-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

/* 神国描述缩进 */
.kingdom-desc {
  margin-top: -5px;

  .item-desc {
    margin-left: 0;
    padding-left: 1.5em;
  }
}

/* 响应式布局 */
@media (max-width: 768px) {
  .god-tier-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }
}
