
/* 技能网格布局 */
.skills-grid {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 20px;
  align-items: start;

  /* 中间分隔线 */
  &::before {
    content: '';
    grid-column: 2;
    grid-row: 1;
    width: 1px;
    height: 100%;
    background-color: var(--theme-border-light);
    justify-self: center;
  }

  /* 有其他技能时改为垂直布局 */
  &.has-other {
    grid-template-columns: 1fr;
    gap: 20px;

    &::before {
      display: none;
    }

    .skills-column {
      grid-column: 1;

      &:not(:first-child) {
        padding-top: 15px;
        border-top: 1px solid var(--theme-border-light);
      }
    }
  }
}

.skills-column {
  display: flex;
  flex-direction: column;
  gap: 10px;

  &:first-child {
    grid-column: 1;
  }

  &:last-child {
    grid-column: 3;
  }
}

.skills-category-title {
  font-family: 'Cinzel', serif;
  font-size: 1em;
  font-weight: 700;
  color: var(--theme-text-tertiary);
  padding-bottom: 8px;
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.empty-message {
  color: var(--theme-text-muted);
  font-style: italic;
  margin: 0;
}

/* 响应式布局 */
@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr;
    gap: 20px;

    &::before,
    &::after {
      display: none;
    }
  }

  .skills-column {
    grid-column: 1 !important;

    &:not(:first-child) {
      padding-top: 10px;
      border-top: 1px solid var(--theme-border-light);
    }
  }
}
