
.tasks-container {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.task-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.task-field {
  margin: 0;
  line-height: 1.6;
  white-space: pre-line;
}

.field-label {
  font-weight: bold;
  color: var(--theme-text-secondary);
  margin-right: 4px;
}

.empty-message {
  color: var(--theme-text-muted);
  font-style: italic;
  margin: 0;
}
