
.item-entry {
  :deep(.summary-title) {
    display: flex;
    align-items: center;
    gap: 4px;
  }
}

.item-details {
  color: var(--theme-text-primary);
  line-height: 1.6;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.item-type,
.item-meta {
  color: var(--theme-text-secondary);
  font-size: 0.95em;
  white-space: pre-line;

  strong {
    color: var(--theme-text-tertiary);
  }
}
