
/* 浮动按钮 - 左上角位置（不贴边） */
.floating-btn {
  position: fixed;
  top: 8px;
  left: 8px;
  width: 46px;
  height: 46px;
  background-color: var(--theme-button-bg);
  color: var(--theme-button-text);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 19px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.25);
  transition: all 0.3s ease;
  z-index: 1000;

  &:hover {
    background-color: var(--theme-button-bg-hover);
    transform: scale(1.1) rotate(90deg);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.35);
  }

  &.active {
    background-color: var(--theme-button-bg-hover);
    transform: rotate(90deg);
  }

  i {
    pointer-events: none;
  }

  /* 移动端适配 */
  @media (max-width: 768px) {
    width: 42px;
    height: 42px;
    font-size: 17px;
    top: 6px;
    left: 6px;
  }
}

/* 模态遮罩 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* 主题面板 */
.theme-panel {
  background-color: var(--theme-background);
  border: 2px solid var(--theme-main-border);
  border-radius: 8px;
  padding: 0;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  animation: slideIn 0.3s ease;
  display: flex;
  flex-direction: column;
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* 面板头部 */
.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background-color: var(--theme-background-secondary);
  border-bottom: 1px solid var(--theme-border-light);
  flex-shrink: 0;

  h3 {
    margin: 0;
    font-size: 1em;
    color: var(--theme-text-secondary);
    font-weight: 700;
    display: flex;
    align-items: center;
    gap: 8px;

    i {
      color: var(--theme-button-bg);
    }

    .theme-name {
      font-weight: 400;
      color: var(--theme-text-muted);
      font-size: 0.9em;
      margin-left: 4px;
    }

    .customized-tag {
      font-weight: 500;
      color: var(--theme-button-bg);
      font-size: 0.85em;
      background-color: rgba(141, 110, 99, 0.1);
      padding: 2px 8px;
      border-radius: 3px;
      margin-left: 4px;
    }
  }

  .close-btn {
    width: 32px;
    height: 32px;
    background-color: transparent;
    color: var(--theme-text-secondary);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    flex-shrink: 0;

    &:hover {
      background-color: var(--theme-title-bg-hover);
      color: var(--theme-text-primary);
    }
  }
}

/* 编辑面板 - 移除外层滚动 */
.editing-panel {
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.group-selector {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  margin-bottom: 12px;

  .group-btn {
    padding: 6px 10px;
    background-color: var(--theme-background-tertiary);
    color: var(--theme-text-secondary);
    border: 1px solid var(--theme-border-light);
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.8em;
    transition: all 0.2s ease;

    &:hover {
      background-color: var(--theme-title-bg-hover);
      border-color: var(--theme-border-dark);
    }

    &.active {
      background-color: var(--theme-button-bg);
      color: var(--theme-button-text);
      border-color: var(--theme-button-bg);
    }
  }
}

.color-editor {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  h4 {
    margin: 0 0 10px 0;
    font-size: 0.9em;
    color: var(--theme-text-secondary);
    font-weight: 600;
    flex-shrink: 0;
  }
}

.color-items {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  overflow-y: auto;
  padding-right: 8px;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: var(--theme-background-tertiary);
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--theme-border-dark);
    border-radius: 4px;

    &:hover {
      background: var(--theme-button-bg);
    }
  }
}

.color-item {
  display: flex;
  flex-direction: column;
  gap: 6px;

  label {
    font-size: 0.8em;
    color: var(--theme-text-secondary);
    font-weight: 600;
  }

  .input-group {
    display: flex;
    gap: 8px;
    align-items: center;

    .color-input {
      width: 50px;
      height: 32px;
      border: 1px solid var(--theme-border-light);
      border-radius: 4px;
      cursor: pointer;
    }

    .text-input {
      flex: 1;
      padding: 6px 8px;
      border: 1px solid var(--theme-border-light);
      border-radius: 4px;
      font-size: 0.8em;
      color: var(--theme-text-primary);
      background-color: var(--theme-background-tertiary);

      &:focus {
        outline: none;
        border-color: var(--theme-button-bg);
      }
    }

    .slider {
      flex: 1;
      cursor: pointer;
    }

    .value-display {
      min-width: 45px;
      font-size: 0.8em;
      color: var(--theme-text-muted);
      text-align: right;
    }
  }
}

/* 操作按钮 */
.actions {
  display: flex;
  gap: 8px;
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid var(--theme-border-light);

  button {
    flex: 1;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    font-size: 0.85em;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
  }

  .btn-primary {
    background-color: var(--theme-button-bg);
    color: var(--theme-button-text);

    &:hover {
      background-color: var(--theme-button-bg-hover);
    }
  }

  .btn-secondary {
    background-color: var(--theme-background-secondary);
    color: var(--theme-text-secondary);
    border: 1px solid var(--theme-border-light);

    &:hover {
      background-color: var(--theme-title-bg-hover);
      border-color: var(--theme-border-dark);
    }
  }
}
