
/* 折叠面板基础样式 */
.details-status {
  margin-bottom: 6px;
  border: 1px solid var(--theme-border-light);
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.25);
  }

  // 锁定状态
  &.locked {
    .summary {
      background-color: var(--theme-disabled-bg);
      color: var(--theme-disabled-text);
      cursor: not-allowed;

      .icon-star {
        color: var(--theme-disabled-text);
      }
    }
  }
}

/* 折叠面板标题 (summary) */
.summary {
  font-family: 'Cinzel', serif;
  font-weight: 700;
  color: var(--theme-text-tertiary);
  background-color: var(--theme-title-bg);
  padding: 4px 15px;
  cursor: pointer;
  list-style: none;
  border-bottom: 1px solid var(--theme-border-dark);
  text-align: left;
  transition:
    background-color 0.2s ease,
    color 0.2s ease;

  &:hover {
    background-color: var(--theme-title-bg-hover);
    color: var(--theme-text-primary);
  }

  .is-open > & {
    background-color: var(--theme-open-bg);
    border-bottom-color: var(--theme-open-border-color);
  }

  // 当摘要需要换行时，调整内边距
  &.has-wrapped-summary {
    padding-bottom: 8px;
  }
}

/* 标题主行（包含图标、标题、箭头） */
.summary-main-line {
  display: flex;
  align-items: center;
  width: 100%;
}

/* 左侧星星图标 */
.icon-star {
  font-size: 1.1em;
  color: var(--theme-star-inactive);
  text-shadow: none;
  margin-right: 12px;
  flex-shrink: 0;
  transform-origin: center center;
  transition:
    transform 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55),
    color 0.4s ease,
    text-shadow 0.4s ease;

  &.is-open {
    transform: rotate(360deg);
    color: var(--theme-star-active);
    text-shadow:
      0 0 3px rgba(255, 255, 255, 0.8),
      0 0 6px var(--theme-star-active),
      0 0 10px #e5a50a;
  }
}

/* 标题文本 */
.summary-title {
  flex: 1;
}

/* 右侧摘要信息（同行显示） */
.summary-details {
  margin-left: 8px;
  padding-right: 15px;
  font-family: 'Noto Sans SC', 'Courier New', monospace;
  font-weight: 500;
  font-size: 0.8em;
  color: var(--theme-text-secondary);
  letter-spacing: 0.5px;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.05);
  align-self: center;
  flex-shrink: 0;
}

/* 摘要信息换行显示 */
.summary-wrapped-details {
  margin-top: 6px;
  padding-left: 22px;
  padding-right: 10px;
  font-family: 'Noto Sans SC', 'Courier New', monospace;
  font-weight: 500;
  font-size: 0.8em;
  color: var(--theme-text-secondary);
  letter-spacing: 0.5px;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.05);
  line-height: 1.4;
}

/* 右侧箭头 */
.arrow-toggle {
  transition: transform 0.2s ease-in-out;
  font-size: 0.8em;
  padding-left: 5px;
  margin-left: auto;
  flex-shrink: 0;

  &.rotated {
    transform: rotate(90deg);
  }
}

/* 折叠内容区域 */
.details-content {
  background-color: var(--theme-background-tertiary);
  opacity: var(--theme-background-tertiary-opacity);
  transition: height 0.2s ease-in-out;
  overflow: hidden;
}

.details-content-inner {
  padding: 15px;
  text-align: left;
  font-size: 0.9em;
}

/* 过渡动画类 */
.collapse-enter-active,
.collapse-leave-active {
  transition: height 0.2s ease-in-out;
  overflow: hidden;
}

.collapse-enter-from,
.collapse-leave-to {
  height: 0 !important;
}

/* 子区块变体样式微调 */
.details-sub-section {
  .details-content-inner {
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

/* 条目变体样式微调（如技能、物品等） */
.details-entry {
  .summary {
    padding-top: 0.8px;
    padding-bottom: 0.8px;
    font-size: 0.9em;
  }

  .details-content-inner {
    padding: 10px 15px;
  }
}
