
/* 资源条区域 */
.resources-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--theme-border-light);
}

/* 状态网格布局 */
.status-grid {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 20px;
  align-items: start;

  /* 中间分隔线 */
  &::before {
    content: '';
    grid-column: 2;
    grid-row: 1;
    width: 1px;
    height: 100%;
    background-color: var(--theme-border-light);
    justify-self: center;
  }
}

.status-grid-left,
.status-grid-right {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

/* 左侧列放在第一列 */
.status-grid-left {
  grid-column: 1;
}

/* 右侧列放在第三列 */
.status-grid-right {
  grid-column: 3;
}

/* 属性名称样式 */
.property-name {
  font-weight: bold;
  color: var(--theme-text-secondary);
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.08);
}

/* 响应式布局 */
@media (max-width: 768px) {
  .status-grid {
    grid-template-columns: 1fr;
    gap: 10px;

    &::before {
      display: none;
    }
  }

  .status-grid-left,
  .status-grid-right {
    grid-column: 1;
  }

  .status-grid-right {
    padding-top: 10px;
    border-top: 1px solid var(--theme-border-light);
  }
}
