
/* 货币部分样式 */
.currency-section {
  margin-bottom: 8px;

  .property-name {
    font-weight: bold;
    color: var(--theme-text-secondary);
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.08);
    margin-bottom: 8px;
  }
}

.currency-display {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 16px;
  padding-left: 15px;
  align-items: center;
}

.currency-item {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  font-size: 0.95em;

  .value-main {
    font-weight: 500;
  }
}

/* 分隔线 */
.thin-divider {
  border: 0;
  border-top: 2px solid var(--theme-border-dark);
  margin: 10px 0;
  width: 100%;
}

/* 物品部分样式 */
.items-section {
  .property-name {
    font-weight: bold;
    color: var(--theme-text-secondary);
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.08);
    margin-bottom: 12px;
  }
}

/* 物品网格布局 - 动态列数 */
.items-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  align-items: start;
}

.items-column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.items-category-title {
  font-family: 'Cinzel', serif;
  font-size: 1em;
  font-weight: 700;
  color: var(--theme-text-tertiary);
  padding-bottom: 8px;
  border-bottom: 1px solid var(--theme-border-light);
  margin-bottom: 6px;
}

.items-list {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.empty-message {
  color: var(--theme-text-muted);
  font-style: italic;
  margin: 0;
  padding-left: 15px;
}

/* 响应式布局 */
@media (max-width: 768px) {
  .items-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .currency-display {
    flex-wrap: wrap;
    gap: 8px;
  }
}
