
/* 装备部分样式 */
.equipment-section {
  .property-name {
    font-weight: bold;
    color: var(--theme-text-secondary);
    text-shadow: 0 0 1px rgba(0, 0, 0, 0.08);
    margin-bottom: 12px;
  }
}

/* 装备网格布局 - 垂直排列 */
.equipment-grid {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.equipment-column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.equipment-category-title {
  font-family: 'Cinzel', serif;
  font-size: 1em;
  font-weight: 700;
  color: var(--theme-text-tertiary);
  padding-bottom: 8px;
  border-bottom: 1px solid var(--theme-border-light);
  margin-bottom: 6px;
}

.equipment-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.empty-category {
  color: var(--theme-text-muted);
  font-style: italic;
  font-size: 0.9em;
  padding: 5px 10px;
}

.empty-message {
  color: var(--theme-text-muted);
  font-style: italic;
  margin: 0;
  padding-left: 15px;
}
